* {
    margin: 0;
    padding: 0;
}

.home {

    width: 80%;
    margin: 0 auto;
    margin-top: 150px;
    height: 60%;
}

.home__container {
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.home__formContainer {
    background: rgba(8, 62, 162, 0.35);
    box-shadow:0px 0px 20px 20px rgba(8, 62, 162, 0.35);
    padding: 30px;
    border-radius: 10px;
    border-radius: 10px;
    width: 500px;
}

.home__title {
    color: rgb(0, 153, 255);
}

.home__container input {
    border: 1px solid white;
    outline: none;
    padding: 15px 10px;
    width: 80%;
    border-radius: 8px;
    margin: 10px 0;
    background: transparent;
    color: white;
    font-size: 14px;
}

.home__formContainer button {
    background: transparent;
    border: 1px solid white;
    outline: none;
    font-size: 18px;
    color: white;
    padding: 10px;
    width: 200px;
    margin-top: 30px;
    border-radius: 12px;
    cursor: pointer;
}