* {
    margin:0;
    padding:2px;
}

nav {
    background-color: teal;
    box-shadow: 0px 0px 10px 20px rgba(205, 212, 236, 0.2);
    color: rgb(158, 28, 49);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;

}

nav ul {
    display: flex;
    justify-content: space-between;
    width: 70%;
    align-items: center;
}

nav h1 {
    color: turquoise;
    font-size: 20px;
    padding-right:100px;
}

nav ul a {
    font-size: 20px;
    font-weight: 300;
    color: rgb(24, 19, 160);
    text-decoration: none;
    font-weight:500;
}
