* {
    margin: 0;
    padding: 0;
}

.user {
    box-shadow: 0px 0px 20px 20px rgba(12, 69, 167, 0.37);
    background-color: rgb(20, 56, 110);
    margin-bottom: 30px;
    width: 500px;
    padding: 20px;
    border-radius: 5px;
    margin-left: 5px;
}

.user h2,
.user h3 {
    color: rgba(247, 246, 247, 0.777);
}

.user span {
    font-weight: 500;
    color: rgb(46, 142, 17);
}

.user__delete {
    background-color: rgb(42, 165, 126);
    color: white;
    padding: 10px 30px;
    border-radius: 5px;
    cursor: pointer;
}

.user__bottom {
    margin-top: 20px;
}